import React from "react";
import { Row, Col } from "react-bootstrap";
import portfolio from "../images/portfolio.png";
import gulvsliping from "../images/gulvsliping.png";

const ProjectsPage = () =>{
    return (
        <section className="projects">
            <span className="projects_title">Projects</span>
            <div className="projects_portfolio">
                <Row>
                    <Col md={6}>
                        <h2 className="projects_name">Portfolio</h2>
                        <div className="projects_wrapper">
                            <img 
                            src={portfolio}
                            alt="portfolio"
                            className="projects_project"
                            />
                            <div className="projects_middle">
                                <a
                                    className="projects_button"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    href="https://rado-st.github.io/"
                                >Preview</a>
                                <a
                                    className="projects_button"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    href="https://github.com/rado-st/rado-st.github.io/"
                                >Source Code</a>
                            </div>
                        </div>
                        
                    </Col>
                    <Col md={6}>
                        <p className="projects_desc">
                            The current porfolio website. <br />
                            Used technology: <br />
                        </p>
                        <ul className="projects_list">
                            <li>HTML5</li>
                            <li>Sass</li>
                            <li>React</li>
                            <li>React Router</li>
                            <li>Bootstrap-React</li>
                            <li>Fortawesome</li>
                            <li>npm</li>
                        </ul>
                    </Col>
                </Row>
                <Row>
                    <Col md={6}>
                        <h2 className="projects_name">Gulvsliping Bergen AS</h2>
                        <div className="projects_wrapper">
                            <img 
                            src={gulvsliping}
                            alt="gulvsliping"
                            className="projects_project"
                            />
                            <div className="projects_middle">
                                <a
                                    className="projects_button"
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    href="https://www.gulvsliping-bergen.no/"
                                >Preview</a>
                            </div>
                        </div>
                        
                    </Col>
                    <Col md={6}>
                        <p className="projects_desc">
                            Gulvsliping Bergen AS - Commercial Project for a Norwegian company. <br />
                            Used technology: <br />
                        </p>
                        <ul className="projects_list">
                            <li>WordPress</li>
                            <li>Elementor</li>
                            <li>Yoast SEO</li>
                            <li>Custom CSS</li>
                        </ul>
                    </Col>
                </Row>
            </div>
        </section>
    )
};

export default ProjectsPage;