import React from "react";
import {NavLink} from "react-router-dom";

class NavHeader extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            openMenu: false,
        }
    }

    handleMenuClick = () =>{
        this.setState({openMenu: !this.state.openMenu})
    }

    render(){
        const {openMenu} = this.state;
        const navList = [
            {name: "Home", path: "/", exact:true},
            {name: "Skills", path:"/skills"},
            {name: "Projects", path: "/projects"},
            {name: "Contact", path: "/contact"}
        ];
        const nav = navList.map(item => (
            <li className="nav-item" key={item.name}>
                <NavLink
                    to={item.path}
                    exact={item.exact ? item.exact : false}
                    onClick={this.handleMenuClick}
                    className="nav-link"
                >
                    {item.name}
                </NavLink>
            </li>
        ))
        return(
            <div>
                <div className="title_hamburger" onClick={this.handleMenuClick}>
                    <div className={`hamburger_line hamburger_top${
                        openMenu ? "--open" : ""
                    }`}></div>
                    <div className={`hamburger_line hamburger_middle${
                        openMenu ? "--open" : ""
                    }`}></div>
                    <div className={`hamburger_line hamburger_bottom${
                        openMenu ? "--open" : ""
                    }`}></div>
                </div>

                <nav className={`header_nav${openMenu ? " header_nav--open": ""}`}>
                    <ul className="nav">{nav}</ul>    
                </nav>       
            </div>
        )
    }
}

export default NavHeader;