import React from "react";
import NavHeader from "../components/NavHeader";
import {Link} from "react-router-dom";


class Header extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            scroll: false
        };
    }

    componentDidMount(){
        window.addEventListener("scroll", this.handleScroll);
    }

    componentWillUnmount(){
        window.removeEventListener("scroll", this.handleScroll);
    }

    handleScroll = e =>{
        if(window.scrollY === 0 && this.state.scroll === true){
            this.setState({scroll: false});
        }else if(window.scrollY !== 0 && this.state.scroll !== true){
            this.setState({scroll: true});
        }
    }

    render(){
        const {scroll} = this.state;
        return(
            <header id="header" className={`header${scroll ? " header--scroll" : ""}`}>
                <div className="title">
                    <span className="title_name">
                        <Link to="/">Radosław Stan</Link>
                    </span>
                    <span className="title_subtitle">Front-end developer</span>
                </div>
                <NavHeader />
                
            </header>
        )
    }
}

export default Header;