import React from "react";
import photo from "../images/profile-pic.jpg";

const HomePage = () =>{
    return(
        <section className="home">
            <div className="home_header">
                <img src={photo} alt="profile" className="home_img" />
            </div>
            <span className="home_title">Hello!</span>
            <p className="home_text">
                My name is Radosław Stan. I have been interested in programming and web development for about 10 years. From 2018 I have started getting commercial
                experience as a front-end developer by building WordPress based websites and small and medium size projects. 
                I mainly worked on projects based on Laravel, but also I have knowledge how to work with Django and Node.js.
                I use Linux for web development and Windows for creating graphics and photo editing. 
            </p>
        </section>
    )
}

export default HomePage;