import React from "react";
import {Row, Col} from "react-bootstrap";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faGithub, faLinkedin} from "@fortawesome/free-brands-svg-icons";
import {faEnvelope, faFilePdf } from "@fortawesome/free-solid-svg-icons";

const ContactPage = () =>{
    return (
        <section className="contact">
            <span className="contact_title">Contact</span>
            <div className="contact_socials">
                <Row>
                    <Col md={6}>
                        <a
                            className="contact_social"
                            target="_blank"
                            rel="noopener noreferrer"
                            href="https://drive.google.com/open?id=1jAd_xRpPPjPBQCeTR2p6ynnwVuz2pV8r"
                        >
                            <FontAwesomeIcon icon={faFilePdf}/>
                            <span className="contact_name">Resume</span>
                        </a>
                    </Col>
                    <Col md={6}>
                        <a
                            className="contact_social"
                            href="mailto:radoslaw.stan96@gmail.com"
                        >
                            <FontAwesomeIcon icon={faEnvelope} />
                            <span className="contact_name">radoslaw.stan96@gmail.com</span>
                        </a>
                    </Col>
                </Row>
                <Row>
                    <Col md={6}>
                        <a
                            className="contact_social"
                            target="_blank"
                            rel="noopener noreferrer"
                            href="https://www.linkedin.com/in/radoslaw-stan"
                        >
                            <FontAwesomeIcon icon={faLinkedin} />
                            <span className="contact_name">linkedin.com/in/radoslaw-stan</span>
                        </a>
                    </Col>
                    <Col md={6}>
                        <a
                            className="contact_social"
                            target="_blank"
                            rel="noopener noreferrer"
                            href="https://github.com/rado-st"
                        >
                            <FontAwesomeIcon icon={faGithub} />
                            <span className="contact_name">github.com/rado-st</span>
                        </a>
                    </Col>
                </Row>
            </div>
        </section>
    )
};

export default ContactPage;